@import '../variables';
@import '../mixins/mixins';
.offline-page-content {
  height:100%;
}
.offline-page-heading {
  font-size: 24px;
  font-weight: bold;
  color: #333;
}
.offline-page-subheading {
  font-size: 18px;
  color: #666;
}
.offline-download-card {
  background-color: #fff;
  box-shadow: 0 10px 10px 0 rgba(0,0,0,0.1);
  padding: 40px;
}
.offline-download-card-heading {
  color: $primary-color;
  font-size: 18px;
  font-weight: bold;
}
.offline-download-card-description{
  font-size: 14px;
  color: #333;
}
.offline-download-card-extra {
  color: $primary-color;
  font-size: 12px;
}
// telemtry & about 
.about-app-heading {
  color: $primary-color;
  font-size: 18px;
  font-weight: bold;
}
.about-app-update-text {
  text-align: right;
  font-size: 14px;
  color: $primary-color;
}
.about-app-left-content {
  text-align: end;
  align-self: flex-end;
}
.offline-telemetry-title {
  color: $primary-color;
  font-size: 14px;
}
.offline-telemetry-clear-title {
  color: #E55A28;
  font-size: 14px;
}
.offline-telemetry-page-title {
  font-size: 14px;
  font-weight: bold;
  color:#fff;
  background: #024F9D;
  padding: 16px;
}
 // offline video
 .offline-watch-video{
  height: 450px;
  width: 790px;
}
.offline-video{
  height: 100%;
  width:100%;
}
.offline-video-heading{
  color: $primary-color;
  font-size: 18px;
  font-weight: bold;
}
.offline-video-title{
  color: $primary-color;
  font-size: 14px;
}
//offline import
.ui.grid.offline-import-content{
padding: 0;
}
.sb-offline-import-content{
background: rgba(0, 136, 64, 0.5);
position: relative;
}
.sb-offline-brake {
width: 1px;
background: rgba(255, 255, 255, 0.5);
position: absolute;
right: 0px;
left: 0px;
top: 50%;
bottom: 0px;
margin: 0px auto;
padding: 0px !important;
z-index: 9;
height: 56px;
transform: translateY(-50%);
color: rgba(255, 255, 255, 0.5);
}
.sb-offline-imports {
.sb-offline-area {
  border: 1px solid #008840;
}
}
.sb-offline-video-demo {
.sb-offline-area{
  border: 1px solid #D0D0D0;
}
}
.sb-offline-area {
align-items: center;
background: #fff;
min-height: 72px;
  border-radius: 4px;
}
.sb-import-icon.sb-import-icon-watch-video{
background-color: rgba(255,161,29,0.1);
}
.sb-btn-secondary.sb-btn-tertiary{
background: $tertiary-100;
border: none;
}
.sb-import-icon {
height: 40px;
width: 40px;
border-radius: 50%;
// background-color: rgba(0,136,64,0.1);
text-align: center;
display: flex;
justify-content: center;
align-items: center;
margin-right: 16px;
i {
  font-size: 32px;
}
}
.sb-offline-import-description-list-key,.sb-offline-import-description-list-value{
font-size: 12px;
color:#333333;
}
.sb-offline-import-description-list-value{
margin-left: 8px;
}
.sb-import-content {
flex: 50%;
.sb-import-content-title {
  color: $blue;
  font-size: 14px;
  line-height: 20px;
  margin-bottom: 0px;
}
.sb-import-content-meta {
  color: $gray;  
  font-size: 12px;    
  line-height: 18px;
}
}


// help page styles

.ui.styled.sb-accordion.accordion,
.ui.styled.sb-accordion.accordion .accordion {
  box-shadow: none;
}
.help-section-title {
  font-size: 16px;
  color: $gray-800;
}

.sb-help-section-subheading {
  color: $gray-800;
  font-size: 12px;
  font-weight: bold;
}

.sb-help-section-extra {
  color: $gray-800;
  font-size: 12px;
}

.sb-help-section-heading {
  font-size: 14px;
  color: $gray-800;
}

.sb-accordion-content {
  background-color: $primary-0;
  padding: ($base-block-space * 2);
}

.sb-accordion-text {
color: $gray-800;
font-size: 14px;
}

.sb-accordion-footer {
  padding: ($base-block-space * 3);
  font-size: 12px;
}

//lists--to be removed later

[dir="ltr"] {
  .sb-circular-list {
    list-style-type: none;
    padding-left: 0;
    .sb-circular-list-item {
      position: relative;   
      padding: $sb-circular-list-item-padding-ltr;
      font-size: $sb-circular-list-item-font-size;    
      &:before {
        content: '';
        height: $sb-circular-list-item-circle-height;
        width: $sb-circular-list-item-circle-width;
        background-color: $sb-circular-list-item-circle-color;
        border-radius: $sb-circular-list-item-circle-border-radius;
        position: absolute;
        left: 0;
        top: $sb-circular-list-item-circle-top;
      }
    }
  }
  .description-list-value {
    margin-left: $sb-description-list-value-margin-left;
  }
}

[dir="rtl"] {
  .sb-circular-list {
    list-style-type: none;
    padding-left: 0;
    .sb-circular-list-item {
      position: relative;   
      padding:$sb-circular-list-item-padding-rtl;
      font-size: $sb-circular-list-item-font-size;    
      &:before {
        content: '';
        height: $sb-circular-list-item-circle-height;
        width: $sb-circular-list-item-circle-width;
        background-color: $sb-circular-list-item-circle-color;
        border-radius: $sb-circular-list-item-circle-border-radius;
        position: absolute;
        right: 0;
        top: $sb-circular-list-item-circle-top;
      }
    }
  }
  .description-list-value {
    margin-right: $sb-description-list-value-margin-right;
  }

}

  .description-list-key {
    color: $sb-description-list-key-color;
    font-size: $sb-description-list-key-font-size;
    font-weight: bold;
      &.description-list-key-small {
         font-size:$sb-description-list-key-small-font-size;
      }
  }
  .description-list-value {
    color: $sb-description-list-value-color;
    font-size: $sb-description-list-value-font-size;
    &.description-list-value-small {
      font-size:$sb-description-list-value-small-font-size;
   }
  }

  // two lines list item 
  .multiline-list-key {
    font-size: $multiline-list-key-font-size;
    color:$multiline-list-key-font-color;
  }
  .multiline-list-value {
    font-size: $multiline-list-value-font-size;
    color:$multiline-list-value-font-color;
    margin-inline-start: 0;
  }


  //bordered list 

  .sb-bordered-list {
    padding-inline-start: 0;
    list-style-type: none;
    border: $sb-bordered-list-border;
    border-radius:$sb-bordered-list-border-radius;
    overflow: hidden;
    .sb-bordered-list-item {
      font-size: $sb-bordered-list-item-font-size;
      padding: $sb-bordered-list-item-padding;
      border-bottom: $sb-bordered-list-item-border-bottom;
      background: $sb-bordered-list-item-border-background;
      .sb-bordered-list-item-title {
        font-size: $sb-bordered-list-item-title-font-size;
        color: $sb-bordered-list-item-title-color;
      }
      .sb-bordered-list-item-title-extra {
        font-size: $sb-bordered-list-item-title-extra-font-size;
        color: $sb-bordered-list-item-title-extra-color;
      }
      .sb-bordered-list-item-heading {
        font-size: $sb-bordered-list-item-heading-font-size;
        color: $sb-bordered-list-item-heading-color;
      }
      .sb-bordered-list-item-subheading {
        font-size:$sb-bordered-list-item-subheading-font-size;
        color:$sb-bordered-list-item-subheading-color;
      }
    }
    .sb-bordered-list-item:first-child {
      padding: $sb-bordered-list-item-first-child-padding;
      background: $sb-bordered-list-item-first-child-background;
    }
    .sb-bordered-list-item:last-child {
      border-bottom: 0;
    }
  } 
